import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Modal from "@material-ui/core/Modal";
// import Cancel from ".././assets/images/closeNew.svg";
import "./printModal.scss";
import CancelIcon from "@material-ui/icons/Cancel";
const useStyles = makeStyles((theme) => ({
  paper: {
    position: "absolute",
    width: "900px",

    backgroundColor: "#ffffff",
    boxShadow: theme.shadows[2],
    height: "auto",
    // overflow: "auto",
    margin: "auto",
    top: "20px",
    padding: "10px",
    borderRadius: "4px",
  },
  nopadding: {
    position: "absolute",

    backgroundColor: "#ffffff",

    margin: "auto",
    top: "3%",
  },
  modalStyle1: {
    position: "absolute",
    top: "10%",
    left: "10%",
    overflow: "scroll",
    height: "auto",
    display: "block",
  },
}));
export default function SimpleModal(props) {
  const classes = useStyles();
  return (
    <div>
      <Modal
        open={props.isShowModal}
        onClose={props.onCloseModal}
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
        onBackdropClick={props.onCloseModal}
        disableAutoFocus={true}
        disableEnforceFocus={true}
        disableRestoreFocus={true}
        className={
          props.className
            ? props.className
            : `${classes.modalStyle1} ${props.className}`
        }
        style={{
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          overflow: "scroll",
          ...props.style,
        }}
      >
        <div className={props.preview ? classes.nopadding : classes.paper}>
          {props.showClose && (
            <div className="close" onClick={props.onCloseModal}>
              <CancelIcon
                style={{ height: "25px", width: "25px", color: "#bdbdbd" }}
              />
            </div>
          )}
          {props.children}
        </div>
      </Modal>
    </div>
  );
}
