import React, { Component } from "react";
import { connect } from "react-redux";
import * as UserActions from "../../store/User/UserAction";
import * as Plandataaction from "../../store/Plandata/PlandataAction";
import { bindActionCreators } from "redux";
import Button from "../../components/Controls/Button/Button";
import "./settings.scss";
import { Card, CardContent } from "@material-ui/core";
import VisibilityIcon from "@material-ui/icons/Visibility";
import { withRouter } from "react-router-dom";
import images from "../../assets/images/schedule/logo1.png";
import Group1 from "../../assets/images/schedule/Group1.svg";
import Group2 from "../../assets/images/schedule/Group2.svg";
import Group3 from "../../assets/images/schedule/Group3.svg";
import AccountBalanceWalletIcon from "@material-ui/icons/AccountBalanceWallet";
import Radio from "@material-ui/core/Radio";
import PrintModal from "../../components/printModal";
import StripeCheckout from "react-stripe-checkout";
import EditIcon from "@material-ui/icons/Edit";
import Modal from "@material-ui/core/Modal";
import { TextField } from "@material-ui/core";
import CloseIcon from "@material-ui/icons/Close";
import ReactGA from "react-ga";

class Settings extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      jobtitle: "",
      jobErr: "",
      isShowImportModal: false,
      editPswd: false,
      oldPassword: "",
      newPassword: "",
      confirmPassword: "",
      submitClicked: false,
    };
    this.handleAdd = this.handleAdd.bind(this);
  }
  componentDidMount() {
    ReactGA.pageview(window.location.pathname);
    let id = localStorage.getItem("id");
    this.props.PlandataAction.getPlanData();
    this.props.UserActions.getSubscription({ userId: id });
  }
  componentDidUpdate(prevProps) {
    const prev = prevProps && prevProps.UserReducer;
    const cur = this.props && this.props.UserReducer;
    if (
      prev.updatePassword !== cur.updatePassword &&
      cur.updatePassword &&
      cur.isUpdatePassword
    ) {
      this.modalCloseHandler();
    }
  }

  handleChange = (e) => {
    this.setState({ jobtitle: e.target.value });
  };
  valiDateFields = () => {
    let validate = true;

    if (this.state.jobtitle === "") {
      validate = false;
      this.setState({ jobErr: "Please enter job title" });
    } else {
      this.setState({ jobErr: "" });
    }

    return validate;
  };
  handleAdd = () => {
    if (this.valiDateFields()) {
      const candid = localStorage.getItem("id");
      const candemail = localStorage.getItem("email");
      const uniqueID = candemail + candid;

      const jobtitle = this.state.jobtitle;

      const payload = {
        uniqueId: uniqueID,
        userID: candid,
        jobtitle: jobtitle,
      };
      this.props.JobDataAction.addJobData(payload);
      this.setState({ jobtitle: "" });
    } else {
    }
  };
  opensubmodel = () => {
    this.setState({ isShowImportModal: true });
  };
  handleImportCancel = () => {
    this.setState({
      isShowImportModal: false,
    });
  };

  initiatePayment = (token, product) => {
    const body = {
      token,
      product,
    };
    const headers = {
      "Content-Type": "application/json",
    };
    return fetch("http://localhost:5007/api/payment/create", {
      method: "POST",
      body: JSON.stringify(body),
      headers,
    })
      .then((response) => {})
      .catch((err) => {});
  };
  changepasswordHandler = () => {
    this.setState({ editPswd: true });
  };
  handlePassword = (e, type) => {
    if (type === "old") {
      this.setState({ oldPassword: e.target.value });
    }
    if (type === "new") {
      this.setState({ newPassword: e.target.value });
    }
    if (type === "confirm") {
      this.setState({ confirmPassword: e.target.value });
    }
  };
  submitClick = () => {
    this.setState({ submitClicked: true });
    const { confirmPassword, oldPassword, newPassword } = this.state;
    if (
      confirmPassword &&
      newPassword &&
      oldPassword &&
      confirmPassword === newPassword
    ) {
      this.props.UserActions.updatePassword({
        password: newPassword,
        confirmPassword: oldPassword,
      });
    }
  };
  modalCloseHandler = () => {
    this.setState({
      editPswd: false,
      oldPassword: "",
      submitClicked: false,
      newPassword: "",
      confirmPassword: "",
    });
  };
  render() {
    var id = localStorage.getItem("id");
    var email = localStorage.getItem("email");
    var name = localStorage.getItem("name");
    var role = localStorage.getItem("role");
    var phone = localStorage.getItem("phone");
    let products = [];
    if (
      this.props &&
      this.props.PlanReducer &&
      this.props.PlanReducer.getPlanData &&
      this.props.PlanReducer.getPlanData.data &&
      this.props.PlanReducer.getPlanData.data.response
    ) {
      this.props.PlanReducer.getPlanData.data.response.map((item) => {
        products.push({
          id: item._id,
          name: item.name,
          price: item.cost,
          currency: item.currency,
          interviewQuota: item.interviewQuota,
          validity: item.validity,
        });
      });
    }
    let sub =
      this.props.UserReducer &&
      this.props.UserReducer.getSubscription &&
      this.props.UserReducer.getSubscription.data &&
      this.props.UserReducer.getSubscription.data.results;

    let totalSub = 0,
      totalRem = 0,
      validity;
    let plan;
    let lastPayment;
    if (sub && sub.length === 1 && sub[0] && sub[0].purchaseDate) {
      lastPayment = sub[0].purchaseDate;
      plan = sub[0] && sub[0].planId;
      let days = plan && plan.validity;
      validity = new Date(lastPayment);
      validity.setDate(validity.getDate() + days);
    } else {
      lastPayment =
        sub &&
        sub.length > 0 &&
        sub.sort((a, b) => new Date(b.purchaseDate) - new Date(a.purchaseDate));
      lastPayment =
        lastPayment && lastPayment[0] && lastPayment[0].purchaseDate;
      plan = lastPayment && lastPayment[0] && lastPayment[0].planId;
      let days = plan && plan.validity;
      validity = new Date(lastPayment);
      validity.setDate(validity.getDate() + days);
    }

    sub &&
      sub.length > 0 &&
      sub.map((el) => {
        totalSub += el && el.usage;
        totalRem += el && el.planId && el.planId.interviewQuota;
      });

    return (
      <div className='row'>
        <div className='resume-card col-3'>
          <div className='res-card1'>
            <CardContent className='res-card-content'>
              <div className='col-md-12 col-lg-12 col-xl-12 mx-auto mb-md-0 mb-4'>
                <div className='user-info'>
                  <img
                    className='img-profile img-circle img-responsive center-block'
                    src='https://bootdey.com/img/Content/avatar/avatar1.png'
                    alt=''
                  />
                  <ul className='meta list list-unstyled'>
                    <li className='name'>{name}</li>
                    <li className='email'>
                      <a href='#'>UserName</a>
                    </li>
                  </ul>
                </div>

                <p>
                  <ul className='meta list list-unstyled'>
                    <li className='name'>{email}</li>
                    <li className='email'>
                      <a href='#'>Email Id</a>
                    </li>
                  </ul>
                </p>
                <p>
                  <ul className='meta list list-unstyled'>
                    <li className='name'>{phone}</li>
                    <li className='email'>
                      <a href='#'>Contact Number</a>
                    </li>
                  </ul>
                </p>
                <p>
                  <ul className='meta list list-unstyled'>
                    <li className='name'>
                      ***********
                      {/* <VisibilityIcon className="eyeicon" /> */}
                      <EditIcon
                        className='editIconSetting'
                        onClick={this.changepasswordHandler}
                      />
                    </li>
                    <li className='email'>
                      <a href='#'>Password</a>
                    </li>
                  </ul>
                </p>
                <p>
                  <ul className='meta list list-unstyled'>
                    <li className='name'>HR Manager</li>
                    <li className='email'>
                      <a href='#'>{role}</a>
                    </li>
                  </ul>
                </p>
              </div>
            </CardContent>
          </div>
        </div>
        <div className='resume-card  col-8'>
          <div className='res-card1'>
            <CardContent className='res-card-content'>
              <div className='row'>
                <div className='row justify-content-around mb-0 pt-5 pb-0 '>
                  <div className=' col-12'>
                    <div className='row justify-content-center'>
                      <div className='col-md-4 col-4 font-italic align-items-center mt-md-3 mt-4'>
                        <h5>
                          <span>
                            <img src={images} className='img-fluid mb-1 ' />
                          </span>
                        </h5>
                        <small
                          className='copy-rights cursor-pointer'
                          style={{ fontWeight: "500" }}
                        >
                          Manage all your interviews, Job hirings and candidates
                          details
                        </small>
                        <ul className='list-unstyled'>
                          <li style={{ fontWeight: "500" }}>
                            Want To Contact Us?
                          </li>
                          <li style={{ fontWeight: "500" }}>008104345678</li>
                          <li style={{ fontWeight: "500" }}>
                            contactbirbal@gmail.com
                          </li>
                        </ul>
                      </div>
                      <div className='col-md-4 col-4 my-sm-0 mt-5'>
                        <ul className='list-unstyled'>
                          <li className='mt-md-3 mt-4 foo-header'>
                            Included in your Plan
                          </li>
                          <li>
                            <Card className='card-style-1'>
                              <CardContent>
                                <div>
                                  <div className='flex-1'>
                                    <div style={{ paddingTop: "10px" }}>
                                      <Radio checked={true} />
                                    </div>
                                    <div style={{ paddingTop: "8px" }}>
                                      <h3 className='para-heading'>
                                        {plan && plan.name}
                                      </h3>
                                      <p className='para-style'>
                                        {plan && plan.interviewQuota} interview
                                        Quota
                                      </p>
                                    </div>
                                    <div>
                                      <h3 className='para-number'>
                                        {plan && plan.cost
                                          ? `$${plan.cost.toFixed(2)}`
                                          : "$0.00"}
                                      </h3>
                                    </div>
                                  </div>
                                </div>
                              </CardContent>
                            </Card>
                          </li>
                          <li>
                            <img src={Group1} />
                            {totalSub} Interview done
                          </li>
                          <li>
                            {" "}
                            <img src={Group2} />
                            {totalRem} Interview Remaning
                          </li>
                          <li>
                            <span className='expon'>
                              {" "}
                              <img src={Group3} /> Expires on -
                            </span>
                            {validity
                              ? new Date(validity).toLocaleDateString()
                              : ""}
                          </li>
                        </ul>
                      </div>
                      <div className='col-md-4 col-4 my-sm-0 mt-5'>
                        <ul className='list-unstyled'>
                          <li className='mt-md-3 mt-4 foo-header'>
                            Billing and payment
                          </li>
                          {/* <li>
                            <AccountBalanceWalletIcon className="iconsvg" />{" "}
                            payment on Sep 12, 2021 Monthly plan
                          </li> */}
                          <li>
                            <AccountBalanceWalletIcon className='iconsvg' />
                            last payment on{" "}
                            {lastPayment &&
                              new Date(lastPayment).toLocaleString("en-US", {
                                year: "numeric",
                                month: "short",
                                day: "numeric",
                              })}{" "}
                            Monthly plan
                          </li>
                          {/* <li>
                            <Button
                              backgroundColor="#04724D"
                              color="#ffffff"
                              width="200px"
                              height="49"
                              fontWeight="bold"
                              className="subscription"
                              onClick={this.opensubmodel}
                              // onClick={() =>
                              //   this.sendSchedule(quesData, data2, quesLength)
                              // }
                            >
                              Renew Subscription
                            </Button>
                          </li> */}
                        </ul>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </CardContent>
            <PrintModal
              isShowModal={this.state.isShowImportModal}
              onCloseModal={this.handleImportCancel}
            >
              <div>
                <div className='row'>
                  {products.map((product, i) => {
                    return (
                      <StripeCheckout
                        stripeKey={process.env.REACT_APP_API_KEY}
                        token={(token) => {
                          this.initiatePayment(token, product);
                        }}
                        name={`Buy ${product.name}`}
                        amount={product.price * 100}
                        email={email}
                        key={i}
                      >
                        <div className='col-md-3'>
                          <Card className='card-style-1'>
                            <CardContent>
                              <div class='row rowcard'>
                                <div class='card-title'>
                                  <h4>{product.name}</h4>
                                  <h3>${product.price}</h3>
                                </div>
                              </div>
                              <p className='cardpara'>
                                Interviews: {product.interviewQuota}
                              </p>
                              <div class='btn-group'>
                                <div class='btn'>
                                  <button>
                                    Buy {product.name} at {product.price}$
                                  </button>
                                </div>
                              </div>
                            </CardContent>
                          </Card>
                        </div>
                      </StripeCheckout>
                    );
                  })}
                </div>
              </div>
            </PrintModal>
          </div>
        </div>
        <Modal
          className='EditPasswordSetting'
          open={this.state.editPswd}
          onClose={this.modalCloseHandler}
          aria-labelledby='simple-modal-title'
          aria-describedby='simple-modal-description'
        >
          <div className='EditModal'>
            <CloseIcon className='close' onClick={this.modalCloseHandler} />
            <div className='editTextField-div'>
              <div className='text-field-input'>
                <TextField
                  id='input-with-icon-textfield'
                  placeholder='Enter Old password'
                  className='inputClass'
                  value={this.state.oldPassword}
                  onChange={(e) => this.handlePassword(e, "old")}
                />

                <div>
                  <small style={{ color: "red", paddingLeft: "6px" }}>
                    {this.state.oldPassword === "" && this.state.submitClicked
                      ? "Enter Old Password"
                      : ""}
                  </small>
                </div>
              </div>
            </div>
            <div className='editTextField-div'>
              <div className='text-field-input'>
                <TextField
                  id='input-with-icon-textfield'
                  placeholder='Enter New password'
                  className='inputClass'
                  value={this.state.newPassword}
                  onChange={(e) => this.handlePassword(e, "new")}
                />

                <div>
                  <small style={{ color: "red", paddingLeft: "6px" }}>
                    {this.state.submitClicked && this.state.newPassword === ""
                      ? "Enter New Password"
                      : ""}
                  </small>
                </div>
              </div>
            </div>
            <div className='editTextField-div'>
              <div className='text-field-input'>
                <TextField
                  id='input-with-icon-textfield'
                  placeholder='Conform password'
                  className='inputClass'
                  value={this.state.confirmPassword}
                  onChange={(e) => this.handlePassword(e, "confirm")}
                />

                <div>
                  <small style={{ color: "red", paddingLeft: "6px" }}>
                    {this.state.confirmPassword === "" &&
                    this.state.submitClicked
                      ? "Enter Confirm Password"
                      : this.state.confirmPassword !== "" &&
                        this.state.submitClicked &&
                        this.state.confirmPassword !== this.state.newPassword
                      ? "Confirm Password and New Password don't match"
                      : ""}
                  </small>
                </div>
              </div>
            </div>
            <div className='button-div'>
              <Button
                variant='contained'
                className='Button'
                onClick={() => this.submitClick()}
                style={{
                  textTransform: "none",
                  fontSize: "20px",
                  fontWeight: "500",
                  borderRadius: "15px",
                  padding: "5px 40px",
                }}
              >
                Submit
              </Button>
            </div>
          </div>
        </Modal>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    UserReducer: state.UserReducer,
    PlanReducer: state.PlanReducer,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    UserActions: bindActionCreators(UserActions, dispatch),
    PlandataAction: bindActionCreators(Plandataaction, dispatch),
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(Settings));
